import axiosClient from "api/common/axiosClient";
import CONFIG from "config/config";
import queryString from "query-string";
import { toast } from "react-toastify";
import i18n from "i18n/i18n";

const soKhopMonHoc = {
  getAll: async (id, payload) => {
    const query = queryString.stringify({ ...payload });
    try {
      const url = `${CONFIG.API.SO_KHOP_MON_HOC.replace(":id", id)}?${query}`;
      return await axiosClient.get(url);
    } catch (error) {
      toast.error(i18n.t("admin.subjects_compare.CHOICE_WARNING"));
    }
  },
  putById: function (idNganhDaoTao, data) {
  	try {
	    const url = `${CONFIG.API.SO_KHOP_MON_HOC.replace(":id", idNganhDaoTao)}`;
	    return axiosClient.put(url, JSON.stringify(data));
    } catch (error) {
      toast.error(i18n.t("admin.subjects_compare.UPDATE_FAILED"));
    }
  },
};

export default soKhopMonHoc;
