import soKhopMonHoc from "api/admin/soKhopMonHoc.api";
import Dnd from "components/common/Dnd/Dnd1";
import TooltipItem from "components/common/Tooltip/TooltipItem/TooltipItem";
import React, { useEffect, useState } from "react";
import Loading from "components/common/loading/Loading";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import trainingMajorApi from "api/common/trainingMajor";
import subjectApi from "api/common/subject";
import AsyncSelect from "react-select/async";
import Select from 'react-select';
import { withFormik } from "formik";
import { useParams } from "react-router";
import i18n from "i18n/i18n";
import * as lodash from "lodash";
import { useTranslation } from "react-i18next";
import { toast } from "react-toastify";
import * as Yup from "yup";
import {
  Form,
  FormGroup,
  FormText,
  Input,
  Label,
  Col,
  Button,
  Card,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane,
  Row,
  Container,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
} from "reactstrap";
import {
  FaExchangeAlt,
  FaPlus,
  FaSyncAlt,
  FaEdit,
  FaTrash,
  FaUserAlt,
  FaUsersCog,
  FaSave

} from "react-icons/fa";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import SubMajorApi from "api/common/subMajor";

const getItems = (count, offset = 0) =>
  Array.from({ length: count }, (v, k) => k).map((k) => ({
    id: `item-${k + offset}`,
    ma: `item-${k + offset}`,
    tenTiengViet: `item ${k + offset}`,
  }));
const initValues = {
  nganhDaoTao: -1,
  khoaTuyenNam1: "",
  khoaTuyenNam2: "",
};
const khoaTuyen = ["2009", "2010", "2011", "2012", "2013", "2014", "2015", "2016", "2017", "2018", "2019", "2020", "2021" ]

const CompareSubjects = function ({
  values,
  handleChange,
  resetForm,
  errors,
}) { 
  const { t } = useTranslation();
  const params = useParams();
  const [data01, setData01] = useState([{id: 0, ma: "", tenTiengViet: ""}]);
  const [data02, setData02] = useState([{id: 0, ma: "", tenTiengViet: ""}]);
  const [listMajor, setListMajor] = useState([]);
  const [majorId, setMajorId] = useState(null);
  const [listSubMajor, setlistSubMajor] = useState([]);
  const [listKhoaTuyen, setListKhoaTuyen] = useState([...khoaTuyen])
  const [optionsSubject, setOptionsSubject] = useState([]);
  const [optionsSubject2, setOptionsSubject2] = useState([]);

  const [isLoading, setIsLoading] = useState(false);
  const [isLoadingBar, setIsLoadingBar] = useState(false);
  const [isComparing, setIsComparing] = useState(false);
  const [isEditing, setIsEditing] = useState(false);

  const [isOpenModalAdd, setIsOpenModalAdd] = useState(false);
  const [isOpenModalAdd2, setIsOpenModalAdd2] = useState(false);

  const handleCloseAddSubject = () => {
        setIsOpenModalAdd(false);
  };

  const handleNewSubjectOnClick = (data) => {
    setIsOpenModalAdd(true)
  }


  // useEffect(() => {
  //     (async () => {
  //         const results = await subjectApi.getAll({
  //             limit: 10,
  //         });
  //         if (results?.contents) {
  //             const formatResults = results?.contents?.map((item) => {
  //                 return {
  //                     label: `${item?.tenTiengViet}` || "",
  //                     value: item?.id,
  //                     data: item,
  //                 };
  //             });
  //             setOptionsSubject(formatResults);
  //         }
  //     })();
  // }, []);
  useEffect(() => {
      (async () => {
          if (Array.isArray(data01)) {
              const formatResults = data01?.map((item) => {
                  return {
                      label: `${item?.tenTiengViet}` || "",
                      value: item?.id,
                      data: item,
                  };
              });
              setOptionsSubject(formatResults);
          }
      })();
  }, [data01]);

  //   useEffect(() => {
  //     (async () => {
  //         if (Array.isArray(data02)) {
  //             const formatResults = data02?.map((item) => {
  //                 return {
  //                     label: `${item?.tenTiengViet}` || "",
  //                     value: item?.id,
  //                     data: item,
  //                 };
  //             });
  //             setOptionsSubject2(formatResults);
  //         }
  //     })();
  // }, [data02]);
  const getListStyle = () => ({
    background: "white",
    padding: "10px",
    margin: "0 0 10px 0",
  });

  const confirmHandleAddItem = (item) => {
    const arr01 = _.cloneDeep(data01);
    arr01.push(item);
    setData01(arr01);
  }

  const confirmHandleAddItem2 = (item) => {
    const arr02 = _.cloneDeep(data02);
    arr02.push(item);
    setData02(arr02);
  }

  const handleRefreshOnClick = ()=>{
    setIsLoadingBar(true);
    handleCompareOnClick();
    setIsEditing(false);
    setIsLoadingBar(false);
  }

  const handleEditOnClick = () =>{
    setIsEditing(true);
  }

  const handleSaveOnClick = async () => {
    setIsLoadingBar(true);
    let dataSend = {
      khoaTuyenNam1: values.khoaTuyenNam1,
      khoaTuyenNam2: values.khoaTuyenNam2,
      contents: []           
    }
    for(let i=0; i < data02.length; i++)
    {    
      if(data02[i].isDragable && data02[i].ma !== "null"){
        if(i >= data01.length){
          dataSend.contents.push({idChiTietGomNhom: data02[i].chiTietGomNhom.id, idChiTietMonHocTrc: null})
        }else{
          if(data01[i].ma==="null")
          {
            dataSend.contents.push({idChiTietGomNhom: data02[i].chiTietGomNhom.id, idChiTietMonHocTrc: null})
          }else {
            dataSend.contents.push({idChiTietGomNhom: data02[i].chiTietGomNhom.id, idChiTietMonHocTrc: data01[i].chiTietGomNhom.id})
          }
        }
      }
    }
    const response = await soKhopMonHoc.putById(parseInt(values.nganhDaoTao, 10), dataSend);
    if(response?.status === 200)
    {
      toast.success(i18n.t("admin.subjects_compare.UPDATE_SUCCESS"));
    }
    handleCompareOnClick();
    setIsLoadingBar(false);
  }
  const handleCompareOnClick = async () => {
    setIsComparing(false);
    setIsLoading(true);
    setIsEditing(false);
    //handle
    try{
      if(values.khoaTuyenNam1 < values.khoaTuyenNam2){
        const data = await soKhopMonHoc.getAll(parseInt(values.nganhDaoTao, 10), {
          khoaTuyenNam1: values.khoaTuyenNam1,
          khoaTuyenNam2: values.khoaTuyenNam2,
        });
        const arr01 = [];
        const arr02 = [];
        if(Array.isArray(data)){
          for (const item of data) {
            if(item.first){
              arr01.push(item?.first);
            }else{
              arr01.push({id:"", ma:"null", tenTiengViet: ""});
            }
            if(item.second.length === 0){
              arr02.push({id:"", ma:"null", tenTiengViet: ""});
            }else{
              arr02.push(item?.second[0]);
            }

            arr01[arr01.length - 1].dropId = `item-${arr01.length - 1}`;
            arr02[arr02.length - 1].dropId = `item-${arr02.length - 1}`;

            arr01[arr01.length - 1].isGroup = false;
            arr02[arr02.length - 1].isGroup = false;
            if(item.second.length > 1)
            {
              if(arr01[arr01.length - 1].ma === arr02[arr02.length - 1].ma){
                  arr01[arr01.length - 1].isDragable = false;
                  arr02[arr02.length - 1].isDragable = false;
                } else {
                  arr01[arr01.length - 1].isDragable = true;
                  arr02[arr02.length - 1].isDragable = true;
                }
              for (let i=1; i<item.second.length; i++) {
                const item1 = JSON.parse(JSON.stringify(arr01[arr01.length - 1]));
                arr01.push(item1);
                arr02.push(item.second[i]);
                if(arr01[arr01.length - 1].ma === arr02[arr02.length - 1].ma){
                  arr01[arr01.length - 1].isDragable = false;
                  arr02[arr02.length - 1].isDragable = false;
                } else {
                  arr01[arr01.length - 1].isDragable = true;
                  arr02[arr02.length - 1].isDragable = true;
                }

                arr01[arr01.length - 1].dropId = `item-${arr01.length - 1}`;
                arr02[arr02.length - 1].dropId = `item-${arr02.length - 1}`;

                arr01[arr01.length - 1].isGroup = true;
                arr02[arr02.length - 1].isGroup = true;
              }
            } else {
              if(arr01[arr01.length - 1].ma === arr02[arr02.length - 1].ma){
                arr01[arr01.length - 1].isDragable = false;
                arr02[arr02.length - 1].isDragable = false;
              } else {
                arr01[arr01.length - 1].isDragable = true;
                arr02[arr02.length - 1].isDragable = true;
              }
            }
          }
          setData01(arr01);
          setData02(arr02);
          setIsComparing(true);
        }
      } else {
        toast.error(i18n.t("admin.subjects_compare.COURSE_CHOISE_ERROR"));
      }
    } catch(e){
      alert(e);
    }
    setIsLoading(false);
  }
  useEffect(async () => {
    setIsLoading(true);
    try {
      const res2 = await trainingMajorApi.getAll();
      if (res2) {
        setListMajor(res2.contents);
      } // eslint-disable-next-line
    } catch (err) {}
    setIsLoading(false);
  }, []);

  useEffect(async () => {
    try {
      if(majorId){
        const listSubMajorRes = await SubMajorApi.getAll({nganhDaoTao:majorId });
        if (listSubMajorRes) {
          const formatData = listSubMajorRes?.contents?.map(item => {
            return item?.khoa;
          })
          setListKhoaTuyen(formatData)
            // setlistSubMajor(listSubMajorRes.contents);
        }
      }else{
        setListKhoaTuyen(khoaTuyen);
      }
       
    } catch (err) {
        // setlistSubMajor([])
    }
}, [majorId]);

  return (
    <div>
    {isLoadingBar && <LoadingBar />}
      <Form className="row container mt-2">
        <FormGroup className="col-lg-4 row">
          <Label for="name">
            {t("admin.subjects_compare.MAJOR")} <AsteriskIcon />
          </Label>
          <Input
            type="select"
            name="nganhDaoTao"
            value={values.nganhDaoTao}
            onChange={(e)=>{handleChange(e);setIsComparing(false); setMajorId(e?.target?.value)}}
          >
            <option key={1} value={-1}>
              {t("admin.subjects_compare.CHOOSE_MAJOR")}
            </option>
            {listMajor?.length > 0 &&
              listMajor?.map((item, index) => {
                return (
                  <option key={index + 1} value={item.id}>
                    {/* {t("admin.subjects_compare.MAJORS_NAME")}:  */}
                    {item?.ten}
                    {" - "}
                    {item?.chuongTrinhDaoTao?.ten}
                  </option>
                );
              })}
          </Input>
        </FormGroup>{" "}
        <br />
        <FormGroup className="col-lg-3 row">
          <Label for="name" >
            {t("admin.subjects_compare.COURSE_1")} <AsteriskIcon />
          </Label>
          <Input
            type="select"
            name="khoaTuyenNam1"
            value={values.khoaTuyenNam1}
            onChange={(e)=>{handleChange(e);setIsComparing(false)}}
          >
            <option key={1} value={-1}>
              {t("admin.subjects_compare.CHOOSE_COURSE_1")}
            </option>
            {listKhoaTuyen.map((item, index) => {
                return (
                  <option key={index + 1} value={item}>
                    {item}
                  </option>
                );
              })}
          </Input>
        </FormGroup>{" "}
        <br />
        <FormGroup className="col-lg-3 row">
          <Label for="name" >
            {t("admin.subjects_compare.COURSE_2")} <AsteriskIcon />
          </Label>
          <Input
            type="select"
            name="khoaTuyenNam2"
            value={values.khoaTuyenNam2}
            onChange={(e)=>{handleChange(e);setIsComparing(false)}}
          >
            <option key={1} value={-1}>
              {t("admin.subjects_compare.CHOOSE_COURSE_2")}
            </option>
            {listKhoaTuyen.map((item, index) => {
                return (
                  <option key={index + 1} value={item}>
                    {item}
                  </option>
                );
              })}
          </Input>
        </FormGroup>{" "}
        <div className="col-lg-2 d-flex justify-content-center">
          <Button color="success" onClick={handleCompareOnClick} >
            <FaExchangeAlt /> {t("admin.subjects_compare.COMPARE")}
          </Button>
        </div>
      </Form>
      
      {isLoading && <div className="mt-3"><Loading /></div>}
      {isComparing && <>
        { data01.length>1 && <>
        {values.khoaTuyenNam2-values.khoaTuyenNam1===1 && 
        <Row className="mt-4">
          <Col className="d-flex justify-content-center">
            <Button color="success" onClick={handleEditOnClick} disabled={isEditing}>
              <FaEdit /> {t("EDIT")}
            </Button>
          </Col>
          <Col className="d-flex justify-content-center">
          {isEditing && 
            <Button color="success" onClick={handleSaveOnClick} >
              <FaSave /> {t("SAVE")}
            </Button>
          }
          </Col>
          <Col className="d-flex justify-content-center">
          {isEditing && 
            <Button color="success" onClick={handleRefreshOnClick} >
              <FaSyncAlt /> {t("admin.common.REFRESH")}
            </Button>
          }
          </Col>
        </Row>}
        {isEditing===true ? 
        <Row className="mt-3">
          <Col className="d-flex justify-content-center">
            <Dnd
              data={data01}
              setData={setData01}
            />
          </Col>
          <Col className="d-flex justify-content-center">
            <Dnd
              data={data02}
              setData={setData02}
            />
          </Col>
        </Row>:<Row className="mt-1">
            <Col className="d-flex justify-content-center">
              <div className="dnd_container">
                <div style={getListStyle()}>
                  {data01.map((item, index) => {if(item!==null) { return (<>
                    {!item.isGroup && <hr style={{height:2, borderWidth:0, color:`gray`, backgroundColor: `gray`}}/>}<TooltipItem key={index} item={item} id={index} /></>
                  )}})}
                </div>
              </div>
            </Col>
            <Col className="d-flex justify-content-center">
              <div className="dnd_container">
                <div style={getListStyle()}>
                  {data02.map((item, index) => {if(item!==null) { return (<>
                    {!item.isGroup && <hr style={{height:2, borderWidth:0, color:`gray`, backgroundColor: `gray`}}/>}<TooltipItem key={index} item={item} id={index} /></>
                  )}})}
                </div>
              </div>
            </Col>
        </Row>}
        {isEditing && <>
        <Row>
          <Col className="mt-3 d-flex justify-content-center">
            <Button color="success" onClick={()=>setIsOpenModalAdd(true)} >
              <FaPlus /> {t("admin.subjects_compare.ADD_SUB")}
            </Button>
          </Col>
          <Col className="mt-3 d-flex justify-content-center">
            <Button color="success" onClick={()=>setIsOpenModalAdd2(true)} >
              <FaPlus /> {t("admin.subjects_compare.ADD_SUB")}
            </Button>
          </Col>
        </Row>
        {
        //   <ModalAddNewOutputStandard
        //         modal={isOpenModalAdd}
        //         toggle={() => setIsOpenModalAdd(!isOpenModalAdd)}
        //         headerTitle={"add new subject"}
        //         options={optionsSubject}
        //         handleOkButtonOnClick={confirmHandleAddItem}
        //         handleCancelButtonOnClick={handleCloseAddSubject}
        //     />
        }
        <ModalAddNewSubject
                modal={isOpenModalAdd}
                options={optionsSubject}
                toggle={() => setIsOpenModalAdd(!isOpenModalAdd)}
                headerTitle={t("admin.subjects_compare.ADD_SUB")}
                handleOkButtonOnClick={confirmHandleAddItem}
                handleCancelButtonOnClick={()=>setIsOpenModalAdd(!isOpenModalAdd)}
            />
        <ModalAddNewSubject
                modal={isOpenModalAdd2}
                options={optionsSubject2}
                toggle={() => setIsOpenModalAdd2(!isOpenModalAdd2)}
                headerTitle={t("admin.subjects_compare.ADD_SUB")}
                handleOkButtonOnClick={confirmHandleAddItem2}
                handleCancelButtonOnClick={()=>setIsOpenModalAdd2(!isOpenModalAdd2)}
            />
        </>}
      </>
      }
      {isEditing && 
      <Row className="m-3 d-flex justify-content-center">
        <Col>
          <Button color="success" onClick={handleSaveOnClick} >
                  <FaSave /> {t("SAVE")}
          </Button>
        </Col>
      </Row>
      }
      </>}
    </div>
  );
};

const CompareSubjectsFormikForm = withFormik({
  mapPropsToValues() {
    return initValues;
  },
  enableReinitialize: true,
  validationSchema: Yup.object().shape({
    nganhDaoTao: Yup.number()
      .required(i18n.t("REQUIRED_FIELD_WARNING"))
      .positive(i18n.t("REQUIRED_FIELD_WARNING")),
  }),
})(CompareSubjects);

export default CompareSubjectsFormikForm;

const ModalAddNewOutputStandard = (props) => {
    const [optionSelected, setOptionSelected] = useState(null);

    useEffect(() => {
        setOptionSelected(null);
    }, []);

    const onChange = (selected) => {
        setOptionSelected(selected);
    };

    const loadOptions = async (inputValue, callback) => {
        const results = await subjectApi.getList({
            page: 1, sorter: { sortType: null, sortBy: null }, searchKey: inputValue,
        });
        let formatResults = [];
        if (results?.contents) {
            formatResults = results?.contents?.map((item) => {
                return {
                    label: `${item?.tenTiengViet}` || "",
                    value: item?.id,
                    data: item,
                };
            });
        }
        callback(formatResults);
    };

    const handleOK = () => {
        if (optionSelected?.data) {
            const formatData = { data: optionSelected.data };
            props.handleOkButtonOnClick(formatData);
            setOptionSelected(null);
        }else{
          const formatData = { data: {id: "", tenTiengViet: "", ma: "null", isDragable: true, dropId: `item-${props.options.length}`}};
          props.handleOkButtonOnClick(formatData);
        }
        props.toggle();
    };

    const handleCancel = () => {
        setOptionSelected(null);
        props.handleCancelButtonOnClick();
    };

    const { t } = useTranslation();

    return (
        <Modal isOpen={props.modal} toggle={props.toggle}>
            <ModalHeader>{props.headerTitle}</ModalHeader>
            <ModalBody>
                <div>
                    <Label>S_u_bj_ec_t</Label>
                    <AsyncSelect
                        cacheOptions
                        defaultOptions={props.options}
                        loadOptions={loadOptions}
                        onChange={onChange}
                        // value={props.value}
                        // onInputChange={(newValue) => setSearch(newValue)}
                    />
                </div>
            </ModalBody>
            <ModalFooter>
                <Button color={`primary`} onClick={handleOK}>
                    {t(props.titleOk || "admin.common.OK")}
                </Button>
                <Button color="warning" onClick={handleCancel}>
                    {t(props.titleCancel || "admin.common.CANCEL")}
                </Button>
            </ModalFooter>
        </Modal>
    );
};
const ModalAddNewSubject = (props) => {
    const [optionSelected, setOptionSelected] = useState(null);
    const { t } = useTranslation();

    useEffect(() => {
        setOptionSelected(null);
    }, []);

    const onChange = (selected) => {
        setOptionSelected(selected);
    };
    const handleOK = () => {
        if (optionSelected?.data) {
            props.handleOkButtonOnClick({... optionSelected.data, dropId: `item-${props.options.length}`, isDragable: true});
            setOptionSelected(null);
        }else{
          const formatData = {id: "", tenTiengViet: "", ma: "null", isDragable: true, dropId: `item-${props.options.length}`};
          props.handleOkButtonOnClick(formatData);
        }
        props.toggle();
    };
    const handleCancel = () => {
        setOptionSelected(null);
        props.handleCancelButtonOnClick();
    };
      return (
        <Modal isOpen={props.modal} toggle={props.toggle}>
            <ModalHeader>{props.headerTitle}</ModalHeader>
            <ModalBody>
                <div>
                    <Label>{t("SUBJECT")}</Label>
                    <Select
                      className="basic-single"
                      classNamePrefix="select"
                      isClearable={true}
                      isSearchable={true}
                      name="color"
                      onChange={onChange}
                      options={props.options}
                      placeholder={t("admin.subjects_compare.BLANK_SUB")}
                    />
                </div>
            </ModalBody>
            <ModalFooter>
                <Button color={`primary`} onClick={handleOK}>
                    {t(props.titleOk || "admin.common.OK")}
                </Button>
                <Button color="warning" onClick={handleCancel}>
                    {t(props.titleCancel || "admin.common.CANCEL")}
                </Button>
            </ModalFooter>
        </Modal>
    );
}