import React, { useEffect, useState } from "react";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import TooltipItem from "components/common/Tooltip/TooltipItem/TooltipItem";
import "./Dnd.css";
import i18n from "i18n/i18n";
const getItems = (count, offset = 0) =>
  Array.from({ length: count }, (v, k) => k).map((k) => ({
    id: `item-${k + offset}`,
    content: `item ${k + offset}`,
  }));
const reorder = (list, startIndex, endIndex) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};
const getItemStyle = (isDragging, draggableStyle) => ({
  // some basic styles to make the items look a bit nicer
  userSelect: "none",
  padding: "10px",
  margin: `0 0 10px 0`,
  // change background colour if dragging
  background: isDragging ? "lightgreen" : "green",
  color: "white",

  // styles we need to apply on draggables
  ...draggableStyle,
});

const getListStyle = (isDraggingOver) => ({
  background: isDraggingOver ? "#74b9ff" : "white",
  padding: "10px",
  margin: "0 0 10px 0",
});
// const getList = () => {
// }
const Dnd = ({ data, setData }) => {

  const onHandleDragEnd = (result) => {
    const { source, destination } = result;
    // dropped outside the list
    if (!destination) {
      return;
    }

    const newData = reorder(
      data,
      source?.index,
      destination?.index
    );
    setData(newData);
  };
  return (
    <div className="dnd_container">
      <div className="dnd_content">
        <DragDropContext onDragEnd={onHandleDragEnd}>
          <Droppable droppableId="droppable">
            {(provided, snapshot) => (
              <div
                {...provided.droppableProps}
                ref={provided.innerRef}
                style={getListStyle(snapshot.isDraggingOver)}
              >
                {data.map((item, index) => {if(item!==null) {
                  if(item.isDragable){
                    return(
                    <>
                    <Draggable key={item.ma+index}
                               draggableId={item.dropId}
                               index={index}>
                      {(provided, snapshot) => (
                        <div
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                          style={getItemStyle(
                            snapshot.isDragging,
                            provided.draggableProps.style
                          )}
                        >
                          {item.ma!=="null" ? `${item.ma} - ${item.tenTiengViet}`: ` - `}
                        </div>
                      )}
                    </Draggable>
                    </>
                )
                  }
                    return (<>
                    <TooltipItem key={index} item={item} id={index} /></>);
                  }})}
                {provided.placeholder}
              </div>
            )}
          </Droppable>
        </DragDropContext>
      </div>
    </div>
  );
};

export default Dnd;
